@import '../../_variables.scss';

.about-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;
    font-family: Arial, sans-serif;
    margin-top: 80px;
}

.about-content {
    display: flex;
    gap: 2rem;
    margin-bottom: 120px;
}

.profile-image {
    flex: 1;

    img {
        width: 100%;
        max-width: 470px;
        max-height: 540px;
        height: auto;
        border-radius: 100px;
        object-fit: cover;
        border: 2px solid #000000
    }
}

.about-text {
    flex: 1;

    .about-title {
        color: $primary-color;
        margin-bottom: 1rem;
        @include H3;
    }

    p {
        @include caption3;
    }
}

.resume-button {
    @include caption3;
    width: 150px;
    height: 52px;
    color: #fff;
    padding: 10px 30px 10px 30px;
    background: var(--Components-Button-background-2, #0158FF);

    img {
        padding-left: 10px;
        width: 24px;
        height: 24px;
    }

    // gap: 0px;
    // border-radius: 56px 0px 0px 0px;
    // border: 2px 0px 0px 0px;
    // opacity: 0px;

}

.skills-title {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    h2 {
        color: $primary-color;
        @include Body2;
    }
}

.skills-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    justify-content: center;
    padding: 0 2rem;
    max-width: 1100px;
}

.skill-item {
    flex: 0 1 auto;
    background-color: #fff;
    padding: 16px 32px;
    gap: 12px;
    border-radius: 100px;
    display: flex;
    align-items: center;
    border: 2px solid #1f1f1f;
    @include caption1;

    img {
        width: 24px;
        height: 24px;
    }
}

.skill-icon {
    font-size: 1.2rem;
}

@media (max-width: 768px) {
    .about-content {
        flex-direction: column;
    }

    .skills-grid {
        gap: 1rem;
    }
}

.exp-and-work {
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px;
    display: flex;
    justify-content: space-between;

    .section {
        width: 48%;

        h4 {
            @include H4;
            color: $primary-color;
            display: flex;
            align-items: center;
            gap: 10px;
        }

        .item {
            margin-bottom: 30px;
            padding-bottom: 8px;
            border-bottom: 1px solid;

            .title {
                @include Body1;
                margin: 16px 0 16px 0;
            }

            .date-company {
                display: flex;
                justify-content: space-between;
                font-size: 14px;

                .date {
                    padding: 3px 20px 3px 20px;
                    gap: 10px;
                    border-radius: 21px;
                    border: 1px solid #1f1f1f;
                }

                .company {
                    color: #8C8C8C;
                    @include caption3_bold;
                }
            }
        }
    }
}

// Add media query for responsiveness
@media (max-width: 768px) {
    .exp-and-work {
        flex-direction: column;

        .section {
            width: 100%;
            margin-bottom: 40px;
        }
    }
}