@import '../../_variables.scss';

.contact-section {

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 200px;
  position: relative;

  background-color: $background-color;

  .title {
    @include H2;
    color: $primary-color;
    margin: 0px;
  }

  .description {
    @include Body2;
    max-width: 670px;

  }

  .email-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;

    .jane-button {
      padding: 15px 50px 15px 50px;
    }

    .arrow-down {
      color: $primary-color;
      padding-bottom: 40px;
    }

    .email {
      @include Title;
      color: #1F1F1F;
      text-decoration: none;
    }

    .leaf-svg {
      position: absolute;
      bottom: 20px;
      left: 20px;
      width: 100px;
      height: 100px;
    }

    .lightning-svg {
      position: absolute;
      top: 20px;
      right: 20px;
      width: 50px;
      height: 50px;
    }

    .footer-text {
      font-size: 0.875rem;
      color: #6B7280;
    }
  }

  .social-icons {
    display: flex;
    gap: 36px;

    .jane-button {
      width: 64px;
      height: 64px;
      padding: 0;
      gap: 16px;
      border-radius: 6px;
      border: 2px 0px 0px 0px;
    }
  }

}